<style lang="scss">
  .font-mask {
    .demo {
      position: relative;
      display: block;
      width: 100%;
      padding-bottom: 50%;
      background: url(../../../assets/demo0.jpg) no-repeat center / cover;
    }
  }
</style>

<template>
  <div class="font-mask">
    <h2><span>Font Mask</span></h2>
    <p>,.</p>
    <p>
      <span class="demo">
        <font-mask text="Fire" color="#ffffff" :h="70" :scale="1.4" :opacity="[50, 20]"></font-mask>
      </span>
    </p>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        doc: [
          {property: 'text', description: '', type: 'String', default: ''},
          {property: 'color', description: ',(,)', type: 'Color', default: '#d3d3d3'},
          {property: 'h', description: '(0-100)', type: 'Number', default: '70'},
          {property: 'fz', description: ',()', type: 'String', default: 'auto'},
          {property: 'scale', description: ', fz ,', type: 'Number', default: '1'},
          {property: 'opacity', description: ',,', type: 'Array / Number', default: '[80, 60]'}
        ]
      };
    },
    methods: {},
    mounted() {}
  }
</script>



