<style lang="scss">
  .button {
    .item {
      float: left;
      margin: 0 .05rem .1rem;
      font-size: .14rem;
      line-height: 2.5;
      border-radius: 4px;
      color: #fff;
    }
    .tip {
      margin: 0 5px 0;
    }
  }
</style>

<template>
  <div class="button">
    <h2><span>Button</span> <span class="chinese"></span></h2>
    <p>,,.native.</p>
    <p class="tip">disabled <a class="keyword" href="https://caniuse.com/#search=filter" target="blank">filter</a> .</p>
    <h3><span>Dependencies</span><span class="chinese"></span></h3>
    <p><o-text type="code" title="base/svg.vue">SVG</o-text> SVG,template.</p>
    <h3><span class="chinese">css.</span></h3>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-button width="6.5em" :type="baseDisabled ? 'error' : 'success'" @click="baseDisabled = !baseDisabled">{{baseDisabled ? 'Disabled' : 'Enable'}}</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled">Default</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="dashed">Dashed</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="dotted">Dotted</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="primary">Primary</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="info">Info</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="warning">Warning</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="error">Error</o-button>
      </div>
      <div class="item">
        <o-button :disabled="baseDisabled" type="success">Success</o-button>
      </div>
    </div>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-button effect="spread" type="info" icon="bilibili" loading="true">Rectangle</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" loading="true">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="dashed">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="dotted">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="primary">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="info">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="warning">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="error">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="success">Spread</o-button>
      </div>
    </div>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-button effect="spread"><o-svg type="bilibili"></o-svg><span>Spread</span></o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="dashed">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="dotted">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="primary">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="info" icon="bilibili" :loading="loading" @click="loading = !loading">Spread</o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="warning"><o-svg type="warn"></o-svg><span>Spread</span></o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="error"><span>Spread</span><o-svg type="error"></o-svg></o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="success"><span>Spread</span><o-svg type="ok"></o-svg></o-button>
      </div>
    </div>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-button effect="spread" shape="circle" icon="bilibili"></o-button>
      </div>
      <div class="item">
        <o-button effect="spread" type="dashed" shape="circle" icon="pic"></o-button>
      </div>
      <div class="item">
        <o-button effect="spread" shape="rectangle" type="info" icon="bilibili">Rectangle</o-button>
      </div>
    </div>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
    <h3><span>Events</span></h3>
    <o-doc :list="eventsDoc" type="event"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        baseDisabled: false,
        loading: false,
        doc: [
          {property: 'type', description: ': primary/info/success/warning/error; : dashed/dotted ', type: 'String', default: ''},
          {property: 'disabled', description: ',hover, ', type: 'Boolean', default: 'false'},
          {property: 'width', description: ',auto/inheritpxem%', type: 'Length', default: 'auto'},
          {property: 'effect', description: ', spread: ', type: 'String', default: ''},
          {property: 'shape', description: ',: circle/rectangle, circleicon', type: 'String', default: ''},
          {property: 'icon', description: ', o-svg, o-svg', type: 'String', default: ''},
          {property: 'for', description: 'labelfor', type: 'String', default: ''},
          {property: 'loading', description: ', loading icon icon, disabled', type: 'Boolean', default: 'false'}
        ],
        eventsDoc: [
          {eventName: 'click', description: '', callbackParamet: ''}
        ]
      };
    },
    methods: {
    }
  }
</script>



