<style lang="scss">
  .select {
    .item {
      width: 60%;
      padding: 0;
      margin: 0 .05rem .1rem;
      font-size: .14rem;
      color: #333;
    }
  }
</style>

<template>
  <div class="select">
    <h2><span>Select</span> <span class="chinese"></span></h2>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-select v-model="t" :data="data" placeholder=""></o-select>
      </div>
      <div class="item">
        <o-select v-model="t" :data="data" placeholder=""  label=""></o-select>
      </div>
      <div class="item">
        <o-select v-model="t" skin="single" :data="data" placeholder=""  label=""></o-select>
      </div>
    </div>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
    <h3><span>Events</span></h3>
    <o-doc :list="eventsDoc" type="event"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        data: [
          {value: 'sfa', label: 'asda'},
          {value: 'sfa2', label: 'as23da'},
          {value: 'sfa3', label: 'asd23a'},
          {value: 'sfa4', label: 'asdsda'},
          {value: 'sfa22', label: 'asdsda'},
          {value: 'sfa42', label: 'adssda'},
          {value: 'sfa32', label: 'afeda'}
        ],
        t: '',
        doc: [
          {property: 'label', description: '', type: 'String', default: ''},
          {property: 'placeholder', description: 'placeholder', type: 'String', default: ''},
          {property: 'readonly', description: '(true)', type: 'Boolean', default: 'false'},
          {property: 'skin', description: ',base,single,class', type: 'String', default: 'base'}
        ],
        eventsDoc: [
          {eventName: 'v-model', description: '(value&input)', callbackParamet: ''}
        ]
      };
    },
    methods: {
    }
  }
</script>



