<style lang="scss">
  .layout {
    p {
      svg {
        height: 1.5em;
        vertical-align: sub;
      }
      a {
        color: inherit;
      }
    }
  }
</style>

<template>
  <div class="layout">
    <h2><span>Layout</span> <span class="chinese"></span></h2>
    <p>.</p>
    <h3><span>Download</span></h3>
    <p><a href="https://github.com/barusu/web/blob/master/src/components/page/components/layout.vue" target="blank"><o-svg type="github"></o-svg>GayHub</a></p>
    <h3><span>Tip</span></h3>
    <p>iframe,.</p>
    <h3><span>Use</span></h3>
    <p>,Ajax,get, post, delete,Jqueryget.</p>
    <p>,,props.</p>
    <p>
      <o-button type="info" @click="isEdit = true"></o-button>
      <o-checkbox v-model="config.borderType"></o-checkbox>
      <o-checkbox v-model="config.listType"></o-checkbox>
    </p>
    <o-layout id="test" :isEdit="isEdit" :config="config" :items="['oUpload', 'oUpload', 'oUpload', 'oUpload']" @saved="isEdit = false" host="http://192.68.68.77:8888/rest/v1/"></o-layout>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
    <h3><span>Update</span></h3>
    <o-log :log="log"></o-log>
  </div>
</template>

<script>
  import oLayout from '@/components/page/components/layout.vue';
  import vColor from '@/components/page/components/color';

  export default {
    components: {oLayout, vColor},
    data() {
      return {
        isEdit: false,
        config: {
          borderType: false,
          listType: true
        },
        height: 500,
        doc: [
          {property: 'id', description: ',,', type: 'String', default: ''},
          {property: 'host', description: ',,:http://192.68.68.77:8888/rest/v1/', type: 'String', default: ''},
          {property: 'isEdit', description: '', type: 'Boolean', default: 'false'},
          {property: 'config', description: ', borderType: , listType: ', type: 'Object', default: '{borderType: false, listType: false }'},
          {property: 'items', description: ', layout.vue', type: 'Array', default: ''}
        ],
        log: [
          {
            date: '2017-02-08',
            log: [
              {text: '.vue', status: 'forgive'},
              {text: 'iframe', status: 'forgive'},
              {text: 'iframe', status: 'forgive'}
            ]
          }
        ]
      };
    },
    methods: {
      resize() {
        if(this.$refs.iframe) {
          this.height = this.$refs.iframe.contentDocument.body.clientHeight || this.$refs.iframe.contentDocument.documentElement.scrollHeight;
        }
        setTimeout(this.resize, 500);
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.resize();
      });
    }
  }
</script>



