<style lang="scss">
  .text {
  }
</style>

<template>
  <div class="text">
    <h2><span>Text</span> <span class="chinese"></span></h2>
    <p>.</p>
    <h3><span>Tooltip</span></h3>
    <p>[title = "String"] title,hover.</p>
    <h5><o-text title="">A Tranquil Night</o-text></h5>
    <p><o-text title="">Beforemybedapoolofnight</o-text></p>
    <p><o-text title="">Canitbehoarfrostontheground</o-text></p>
    <p><o-text title="">Lookingup,Ifindthemoonbright</o-text></p>
    <p><o-text title="">Bowing,InhomesicknessImdrowned</o-text></p>
    <h3><span>Color & Type</span></h3>
    <p>,.</p>
    <p><o-text conceal> </o-text> [conceal] ,hover,Tooltip().</p>
    <p><o-text hidden> </o-text> [hidden] ,.</p>
    <p><o-text transparent> </o-text> [transparent] ,.</p>
    <p>[type = "title"] <o-text type="title">38</o-text></p>
    <p>[type = "tag"] <o-text type="tag"></o-text></p>
    <p>[type = "code"] <o-text type="code">String</o-text><o-text type="code">Number</o-text></p>
    <p>[type = "time"] <o-text type="time">2017-02-10</o-text></p>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        doc: [
          {property: 'type', description: ': title/tag/time; : code', type: 'String', default: ''},
          {property: 'title', description: ',', type: 'String', default: ''},
          {property: 'transparent', description: ',', type: '', default: ''},
          {property: 'hidden', description: ',', type: '', default: ''},
          {property: 'conceal', description: ',', type: '', default: ''}
        ]
      };
    },
    methods: {}
  }
</script>



