<style lang="scss">
  .checkbox {
    .item {
      padding: 0 1em;
      margin: 0 .05rem .1rem;
      font-size: .14rem;
    }
  }
</style>

<template>
  <div class="checkbox">
    <h2><span>Checkbox</span> <span class="chinese"></span></h2>
    <p>.</p>
    <p>Safaritransition,transform-origin50% 50% 0,,1s.</p>
    <p>: MacBook Pro (Retina, 13-inch, Early 2015) Safari Version 11.0.2</p>
    <div class="clearfix">
      <div class="item">
        <o-checkbox disabled>disabled</o-checkbox>
        <o-checkbox>test</o-checkbox>
        <o-checkbox indeterminate>indeterminate</o-checkbox>
      </div>
    </div>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
    <h3><span>Events</span></h3>
    <o-doc :list="eventsDoc" type="event"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        doc: [
          {property: 'disabled', description: '', type: 'Boolean', default: 'false'},
          {property: 'indeterminate', description: '(false)', type: 'Boolean', default: 'false'}
        ],
        eventsDoc: [
          {eventName: 'v-model', description: '(value&input)', callbackParamet: ''}
        ]
      };
    },
    methods: {
    }
  }
</script>



