<style lang="scss">
  .input {
    .item {
      width: 60%;
      padding: 0;
      margin: 0 .05rem .1rem;
      font-size: .14rem;
      color: #333;
    }
  }
</style>

<template>
  <div class="input">
    <h2><span>Input</span> <span class="chinese"></span></h2>
    <p>.</p>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="t" placeholder=""></o-input>
      </div>
      <div class="item">
        <o-input v-model="t" label=""></o-input>
      </div>
      <div class="item">
        <o-input v-model="t" skin="single" label=""></o-input>
      </div>
    </div>
    <h3><span>API</span></h3>
    <o-doc :list="doc"></o-doc>
    <h3><span>Events</span></h3>
    <o-doc :list="eventsDoc" type="event"></o-doc>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        t: '',
        doc: [
          {property: 'label', description: '', type: 'String', default: ''},
          {property: 'placeholder', description: 'placeholder', type: 'String', default: ''},
          {property: 'readonly', description: '(true)', type: 'Boolean', default: 'false'},
          {property: 'skin', description: ',base,single,class', type: 'String', default: 'base'}
        ],
        eventsDoc: [
          {eventName: 'v-model', description: '(value&input)', callbackParamet: ''},
          {eventName: 'blur', description: '', callbackParamet: ''}
        ]
      };
    },
    methods: {
    }
  }
</script>



