<style lang="scss">
  .bill {
    .item {
      float: left;
      width: 25%;
      padding: 0;
      margin: 0 .05rem .1rem;
      font-size: .14rem;
      line-height: 2.5;
      border-radius: 4px;
      color: #333;
      border: 1px solid transparent;
    }
  }
</style>

<template>
  <div class="bill">
    <h2><span>Bill</span></h2>
    <p>Base Info.</p>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="ship.no" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.name" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-select v-model="ship.status" label="" type="single" :data="status"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.type" label="" type="single" :data="type"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.camp" label="" type="single" :data="camp"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.rarity" label="" type="single" :data="rarity"></o-select>
      </div>
      <div class="item">
        <o-input v-model="ship.buildTime" label="" type="single"></o-input>
      </div>
    </div>
    <p>Property</p>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="ship.hp" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-select v-model="ship.armor" label="" type="single" :data="armor"></o-select>
      </div>
      <div class="item">
        <o-input v-model="ship.fill" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.fire" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.torpedo" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.dodge" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.air" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.airDef" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.oil" label="" type="single" readonly></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.speed" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.luck" label="" type="single"></o-input>
      </div>
    </div>
    <p>Equip</p>
    <div class="clearfix">
      <div class="item">
        <o-select v-model="ship.equip1" label="1" type="single" :data="equip"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.equip2" label="2" type="single" :data="equip"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.equip3" label="3" type="single" :data="equip"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.equip4" label="4" type="single" :data="equip"></o-select>
      </div>
      <div class="item">
        <o-select v-model="ship.equip5" label="5" type="single" :data="equip"></o-select>
      </div>
    </div>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="ship.equipEffic1" label="1" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.equipEffic2" label="2" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.equipEffic3" label="3" type="single"></o-input>
      </div>
    </div>
    <p>Images</p>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="ship.img" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.reform" label="" type="single"></o-input>
      </div>
      <div class="item">
        <o-input v-model="ship.marry" label="" type="single"></o-input>
      </div>
    </div>
    <p>Other</p>
    <div class="clearfix">
      <div class="item">
        <o-input v-model="ship.oilCorrection" label="" type="single"></o-input>
      </div>
    </div>
    <div class="clearfix">
      <div class="item">
        <o-button @click="save"></o-button>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from '@/libs/ajax';

  const rarityOil = {N: 0, R: 1, SR: 2, SSR: 3, UR: 4};
  const typeOil = {DD: 1, CL: 2, CA: 3, BB: 6, BC: 5, CVL: 3, CV: 4, BBV: 6, AR: 2};

  function getEmptyShip() {
    return {
      no: '',
      name: '',
      type: '',
      camp: '',
      buildTime: '',
      rarity: '',
      armor: 'LA',
      hp: '',
      fill: '',
      fire: '',
      torpedo: '',
      dodge: '',
      air: '',
      airDef: '',
      oil: '',
      speed: '',
      luck: '',
      equip1: '',
      equip2: '',
      equip3: 'ADA',
      equip4: 'EQ',
      equip5: 'EQ',
      equipEffic1: '',
      equipEffic2: '',
      equipEffic3: '',
      img: '',
      marry: '',
      reform: '',
      skin: '[]',
      oilCorrection: '',
      skill: ''
    };
  }

  export default {
    data() {
      return {
        ship: getEmptyShip(),
        type: [
          {value: 'DD', label: ''},
          {value: 'CL', label: ''},
          {value: 'CA', label: ''},
          {value: 'BB', label: ''},
          {value: 'BC', label: ''},
          {value: 'CVL', label: ''},
          {value: 'CV', label: ''},
          {value: 'BBV', label: ''},
          {value: 'AR', label: ''}
        ],
        status: [
          {value: 'F', label: ''},
          {value: 'FR', label: ''},
          {value: 'HR', label: ''}
        ],
        camp: [
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''},
          {value: '', label: ''}
        ],
        rarity: [
          {value: 'N', label: ' '},
          {value: 'R', label: ' '},
          {value: 'SR', label: ' '},
          {value: 'SSR', label: ' '},
          {value: 'UR', label: ' '}
        ],
        armor: [
          {value: 'LA', label: ''},
          {value: 'MA', label: ''},
          {value: 'HA', label: ''}
        ],
        equip: [
          {value: 'EQ', label: ''},
          {value: 'ADA', label: ''},
          {value: 'TD', label: ''},
          {value: 'DDG', label: ''},
          {value: 'CLG', label: ''},
          {value: 'CAG', label: ''},
          {value: 'BBG', label: ''},
          {value: 'FP', label: ''},
          {value: 'BP', label: ''},
          {value: 'TP', label: ''},
          {value: 'WS', label: ''}
        ]
      };
    },
    watch: {
      'ship.type': function(value) {
        switch(value) {
        case 'DD':
          this.ship.equip1 = 'DDG';
          this.ship.equip2 = 'TD';
          break;
        case 'CL':
          this.ship.equip1 = 'CLG';
          this.ship.equip2 = 'TD';
          break;
        case 'CA':
          this.ship.equip1 = 'CAG';
          this.ship.equip2 = 'TD';
          break;
        case 'BB':
          this.ship.equip1 = 'BBG';
          this.ship.equip2 = 'CLG';
          break;
        case 'BC':
          this.ship.equip1 = 'BBG';
          this.ship.equip2 = 'DDG';
          break;
        case 'CVL':
          this.ship.equip1 = '';
          this.ship.equip2 = '';
          break;
        case 'CV':
          this.ship.equip1 = 'FP';
          this.ship.equip2 = 'BP';
          this.ship.equip3 = 'TP';
          break;
        case 'BBV':
          this.ship.equip1 = 'BBG';
          this.ship.equip2 = 'WS';
          break;
        case 'AR':
          this.ship.equip1 = 'EQ';
          this.ship.equip2 = 'ADA';
          break;
        default:
        }
        this.updateOil();
      },
      'ship.status': function(value) {
        if(value === 'FR') this.ship.oilCorrection = -1;
        this.updateOil();
      },
      'ship.rarity': 'updateOil',
      'ship.oilCorrection': 'updateOil'
    },
    methods: {
      updateOil() {
        if(this.ship.rarity && typeOil[this.ship.type]) {
          this.ship.oil = rarityOil[this.ship.rarity] + typeOil[this.ship.type] + 6;
          if(this.ship.oilCorrection) this.ship.oil += parseInt(this.ship.oilCorrection);
        }
      },
      save() {
        $.post('resource/azurlane/add', this.ship, data => {
          if(data == '1') {
            this.$msg.success('');
            this.ship = getEmptyShip();
          }else {
            this.$msg.error('');
          }
        });
      }
    }
  }
</script>



